<template>
	<div class="diy_edit page_application_for_studying_abroad" id="application_for_studying_abroad_edit">
		<div class='warp'>
			<div class='container'>
				<div class='row diy_edit_content_box'>
						<div v-if="$check_field('set','name_of_institution') || $check_field('add','name_of_institution') || $check_field('get','name_of_institution')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								院校名称:
							</span>
						</div>
								<!-- 文本 -->
									<div class="diy_field diy_text">
							<input type="text" id="form_name_of_institution" v-model="form['name_of_institution']" placeholder="请输入院校名称" v-if="(form['name_of_institution'] && $check_field('set','name_of_institution')) || (!form['name_of_institution'] && $check_field('add','name_of_institution'))"  :disabled="disabledObj['name_of_institution_isDisabled']"/>
							<span v-else-if="$check_field('get','name_of_institution')">{{ form['${obj.name}'] }}</span>
						</div>
										</div>
							<div v-if="$check_field('set','major_name') || $check_field('add','major_name') || $check_field('get','major_name')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								专业名称:
							</span>
						</div>
								<!-- 文本 -->
									<div class="diy_field diy_text">
							<input type="text" id="form_major_name" v-model="form['major_name']" placeholder="请输入专业名称" v-if="(form['major_name'] && $check_field('set','major_name')) || (!form['major_name'] && $check_field('add','major_name'))"  :disabled="disabledObj['major_name_isDisabled']"/>
							<span v-else-if="$check_field('get','major_name')">{{ form['${obj.name}'] }}</span>
						</div>
										</div>
							<div v-if="$check_field('set','tuition_information') || $check_field('add','tuition_information') || $check_field('get','tuition_information')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								学费信息:
							</span>
						</div>
								<!-- 文本 -->
									<div class="diy_field diy_text">
							<input type="text" id="form_tuition_information" v-model="form['tuition_information']" placeholder="请输入学费信息" v-if="(form['tuition_information'] && $check_field('set','tuition_information')) || (!form['tuition_information'] && $check_field('add','tuition_information'))"  :disabled="disabledObj['tuition_information_isDisabled']"/>
							<span v-else-if="$check_field('get','tuition_information')">{{ form['${obj.name}'] }}</span>
						</div>
										</div>
							<div v-if="$check_field('set','declaration_personnel') || $check_field('add','declaration_personnel') || $check_field('get','declaration_personnel')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								申报人员:
							</span>
						</div>
						<div class="diy_field diy_down">
							<select id="form_declaration_personnel" :disabled="disabledObj['declaration_personnel_isDisabled']" v-model="form['declaration_personnel']" v-if="(form['declaration_personnel'] && $check_field('set','declaration_personnel')) || (!form['declaration_personnel'] && $check_field('add','declaration_personnel'))" >
								<option v-for="o in list_user_declaration_personnel" :value="o['user_id']">
									{{o['nickname'] + '-' + o['username']}}
								</option>
							</select>
							<span v-else-if="$check_field('get','declaration_personnel')">{{ form['declaration_personnel'] }}</span>
						</div>
					</div>
							<div v-if="$check_field('set','student_name') || $check_field('add','student_name') || $check_field('get','student_name')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								学生姓名:
							</span>
						</div>
								<!-- 文本 -->
									<div class="diy_field diy_text">
							<input type="text" id="form_student_name" v-model="form['student_name']" placeholder="请输入学生姓名" v-if="(form['student_name'] && $check_field('set','student_name')) || (!form['student_name'] && $check_field('add','student_name'))"  :disabled="disabledObj['student_name_isDisabled']"/>
							<span v-else-if="$check_field('get','student_name')">{{ form['${obj.name}'] }}</span>
						</div>
										</div>
							<div v-if="$check_field('set','date_of_declaration') || $check_field('add','date_of_declaration') || $check_field('get','date_of_declaration')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								申报日期:
							</span>
						</div>
								<!-- 日期 -->
						<div class="diy_field diy_date">
							<input type="date" :disabled="disabledObj['date_of_declaration_isDisabled']" id="form_date_of_declaration" v-model="form['date_of_declaration']" placeholder="请输入申报日期" v-if="(form['date_of_declaration'] && $check_field('set','date_of_declaration')) || (!form['date_of_declaration'] && $check_field('add','date_of_declaration'))" />
							<span v-else-if="$check_field('get','date_of_declaration')">{{ form['${obj.name}'] }}</span>
						</div>
							</div>
							<div v-if="$check_field('set','application_materials') || $check_field('add','application_materials') || $check_field('get','application_materials')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								申报材料:
							</span>
						</div>
								<!-- 文件 -->
						<input type="file" style="display: none;" id="form_file_application_materials" title="application_materials" @change="change_file($event.target.files,'application_materials')"/>
						<!-- 修改权限 -->
						<div class="diy_field diy_img" v-if="form['application_materials'] && $check_field('set','application_materials')">
							<label for="form_file_application_materials">
								<!--<span>{{form['application_materials']}} </span>-->
								<a :href="$fullUrl(form['application_materials'])" target="_blank" style="color: rgb(64, 158, 255);">点击下载</a>
							</label>
						</div>
						<!-- 添加权限 -->
						<div class="diy_field diy_img" v-else-if="!form['application_materials'] && $check_field('add','application_materials')">
							<label for="form_file_application_materials">
								<div class="btn_add_img">
									<span>+</span>
								</div>
							</label>
						</div>
						<!-- 查询权限 -->
						<div class="diy_field diy_img" v-else-if="$check_field('get','application_materials')">
							<span>{{form['application_materials']}} </span>
						</div>
							</div>
							<div v-if="$check_field('set','contact_information') || $check_field('add','contact_information') || $check_field('get','contact_information')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								联系方式:
							</span>
						</div>
								<!-- 文本 -->
									<div class="diy_field diy_text">
							<input type="text" id="form_contact_information" v-model="form['contact_information']" placeholder="请输入联系方式" v-if="(form['contact_information'] && $check_field('set','contact_information')) || (!form['contact_information'] && $check_field('add','contact_information'))"  :disabled="disabledObj['contact_information_isDisabled']"/>
							<span v-else-if="$check_field('get','contact_information')">{{ form['${obj.name}'] }}</span>
						</div>
										</div>
							<div v-if="$check_field('set','e_mail_address') || $check_field('add','e_mail_address') || $check_field('get','e_mail_address')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								邮箱地址:
							</span>
						</div>
								<!-- 文本 -->
									<div class="diy_field diy_text">
							<input type="text" id="form_e_mail_address" v-model="form['e_mail_address']" placeholder="请输入邮箱地址" v-if="(form['e_mail_address'] && $check_field('set','e_mail_address')) || (!form['e_mail_address'] && $check_field('add','e_mail_address'))"  :disabled="disabledObj['e_mail_address_isDisabled']"/>
							<span v-else-if="$check_field('get','e_mail_address')">{{ form['${obj.name}'] }}</span>
						</div>
										</div>
							<div v-if="$check_field('set','remarks') || $check_field('add','remarks') || $check_field('get','remarks')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								备注信息:
							</span>
						</div>
								<!-- 多文本 -->
						<div class="diy_field diy_desc">
							<textarea id="form_remarks" v-model="form['remarks']" v-if="(form['remarks'] && $check_field('set','remarks')) || (!form['remarks'] && $check_field('add','remarks'))" :disabled="disabledObj['remarks_isDisabled']" />
							<span v-else-if="$check_field('get','remarks')">{{ form['${obj.name}'] }}</span>
						</div>
							</div>
	


					<div v-if="user_group === '管理员' || $check_examine()" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								审核状态:
							</span>
						</div>
						<div class="diy_field diy_select" v-if="$check_action('/application_for_studying_abroad/edit','examine')">
							<!--<span> {{ form['examine_state'] }} </span>-->
							<select v-model="form['examine_state']">
								<option value="未审核">
									未审核
								</option>
								<option value="已通过">
									已通过
								</option>
								<option value="未通过">
									未通过
								</option>
							</select>
						</div>
						<div class="diy_field diy_text" v-else>
							<span>
								{{ form['examine_state'] }}
							</span>
						</div>
					</div>
					<div v-if="user_group === '管理员' || $check_examine()" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								审核回复:
							</span>
						</div>
						<div class="diy_field diy_desc" v-if="$check_action('/application_for_studying_abroad/edit','examine')">
							<textarea v-model="form['examine_reply']"></textarea>
						</div>
						<div class="diy_field diy_text" v-else>
							<span>
								{{ form['examine_reply'] }}
							</span>
						</div>
					</div>


				</div>
				<div class="diy_edit_submit_box row">
					<div class="col-12">
						<div class="btn_box">
							<button class="btn_submit" @click="submit()">提交</button>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import mixin from "@/mixins/page.js";
	export default {
		mixins: [mixin],
		components: {},
		data() {
			return {
				url_get_obj: "~/api/application_for_studying_abroad/get_obj?",
				url_add: "~/api/application_for_studying_abroad/add?",
				url_set: "~/api/application_for_studying_abroad/set?",

				// 登录权限
				oauth: {
					"signIn": true,
					"user_group": []
				},

				// 查询条件
				query: {
						"name_of_institution": "",
							"major_name": "",
							"tuition_information": "",
							"declaration_personnel": 0,
							"student_name": "",
							"date_of_declaration": "",
							"application_materials": "",
							"contact_information": "",
							"e_mail_address": "",
							"remarks": "",
						"application_for_studying_abroad_id": 0,
				},

				obj: {
						"name_of_institution":  '', // 院校名称
							"major_name":  '', // 专业名称
							"tuition_information":  '', // 学费信息
							"declaration_personnel": 0, // 申报人员
							"student_name":  '', // 学生姓名
							"date_of_declaration": new Date().getTime(),
							"application_materials":  '', // 申报材料
							"contact_information":  '', // 联系方式
							"e_mail_address":  '', // 邮箱地址
							"remarks":  '', // 备注信息
						"examine_state": "未审核",
					"examine_reply": "",
					"application_for_studying_abroad_id": 0,
				},

				// 表单字段
				form: {
						"name_of_institution":  '', // 院校名称
							"major_name":  '', // 专业名称
							"tuition_information":  '', // 学费信息
							"declaration_personnel": 0, // 申报人员
							"student_name":  '', // 学生姓名
							"date_of_declaration": new Date().getTime(),
							"application_materials":  '', // 申报材料
							"contact_information":  '', // 联系方式
							"e_mail_address":  '', // 邮箱地址
							"remarks":  '', // 备注信息
						"examine_state": "未审核",
					"examine_reply": "",
					"application_for_studying_abroad_id": 0,
				},
				disabledObj:{
						"name_of_institution_isDisabled": false,
							"major_name_isDisabled": false,
							"tuition_information_isDisabled": false,
							"declaration_personnel_isDisabled": false,
							"student_name_isDisabled": false,
							"date_of_declaration_isDisabled": false,
							"application_materials_isDisabled": false,
							"contact_information_isDisabled": false,
							"e_mail_address_isDisabled": false,
							"remarks_isDisabled": false,
					},

												// 用户列表
				list_user_declaration_personnel: [],
													
				// ID字段
				field: "application_for_studying_abroad_id",

			}
		},
		methods: {
			
				
				
						/**
			 * 获取学生用户用户列表
			 */
			async get_list_user_declaration_personnel() {
				// if(this.user_group !== "管理员" && this.form["declaration_personnel"] === 0) {
				//     this.form["declaration_personnel"] = this.user.user_id;
				// }
				var json = await this.$get("~/api/user/get_list?user_group=学生用户");
				if(json.result && json.result.list){
					this.list_user_declaration_personnel = json.result.list;
				}
				else if(json.error){
					console.error(json.error);
				}
			},
					async get_user_session_declaration_personnel(){
				var _this = this;
				var json = await this.$get("~/api/user_group/get_obj?name=学生用户");
				if(json.result && json.result.obj){
					var source_table = json.result.obj.source_table;
					var user_id = _this.$store.state.user.user_id;
					if (user_id){
						var url = "~/api/"+source_table+"/get_obj?"
						this.$get(url, {"user_id":_this.$store.state.user.user_id}, function(res) {
							if (res.result && res.result.obj) {
								var arr = []
								for (let key in res.result.obj) {
									arr.push(key)
								}
								var arrForm = []
								for (let key in _this.form) {
									arrForm.push(key)
								}
								_this.form["declaration_personnel"] = user_id
								_this.disabledObj['declaration_personnel' + '_isDisabled'] = true
								for (var i=0;i<arr.length;i++){
                  if (arr[i]!=='examine_state' && arr[i]!=='examine_reply') {
                    for (var j = 0; j < arrForm.length; j++) {
                      if (arr[i] === arrForm[j]) {
                        if (arr[i] !== "declaration_personnel") {
                          _this.form[arrForm[j]] = res.result.obj[arr[i]]
                          _this.disabledObj[arrForm[j] + '_isDisabled'] = true
                          break;
                        }
                      }
                    }
                  }
								}
							}
						});
					}
				}
				else if(json.error){
					console.error(json.error);
				}
			},
	
				
				
				
				
				
				
	
			/**
			 * 修改文件
			 * @param { Object } files 上传文件对象
			 * @param { String } str 表单的属性名
			 */
			change_file(files, str) {
				var form = new FormData();
				form.append("file", files[0]);
				this.$post("~/api/application_for_studying_abroad/upload?", form, (res) => {
					if (res.result) {
						this.form[str] = res.result.url;
					} else if (res.error) {
						this.$toast(res.error.message);
					}
				});
			},

			/**
			 * 获取对象后获取缓存表单
			 * @param {Object} json
			 * @param {Object} func
			 */
			get_obj_before(param){
				var form = $.db.get("form");
				// if (form) {
        //   delete(form.examine_state)
        //   delete(form.examine_reply)
        //   this.obj = $.push(this.obj ,form);
				// 	this.form = $.push(this.form ,form);
				// }
				// var arr = []
				// for (let key in form) {
				// 	arr.push(key)
				// }
				// for (var i=0;i<arr.length;i++){
				// 	this.disabledObj[arr[i] + '_isDisabled'] = true
				// }
        if (form) {
          var arr = []
          for (let key in form) {
            arr.push(key)
          }
          var arrForm = []
          for (let key in this.form) {
            arrForm.push(key)
          }
          for (var i=0;i<arr.length;i++){
            if (arr[i]!=='examine_state' && arr[i]!=='examine_reply') {
              for (var j = 0; j < arrForm.length; j++) {
                if (arrForm[j] === arr[i]) {
                  this.form[arrForm[j]] = form[arr[i]]
                  this.obj[arrForm[j]] = form[arr[i]]
                  this.disabledObj[arrForm[j] + '_isDisabled'] = true
                  break;
                }
              }
            }
          }
        }
												        if (JSON.stringify(this.form["date_of_declaration"]).indexOf("-")===-1) {
          this.form["date_of_declaration"] = this.$toTime(parseInt(this.form["date_of_declaration"]), "yyyy-MM-dd")
        }
									
        $.db.del("form");
				return param;
			},

			/**
			 * 获取对象后获取缓存表单
			 * @param {Object} json
			 * @param {Object} func
			 */
			get_obj_after(json ,func){
				// var form = $.db.get("form");
				// var obj = Object.assign({} ,form ,this.obj);
				// if (obj) {
        //   delete(obj.examine_state)
        //   delete(obj.examine_reply)
				// 	this.obj = $.push(this.obj ,obj);
				// }
				// if (form) {
        //   delete(form.examine_state)
        //   delete(form.examine_reply)
				// 	this.form = $.push(this.form ,form);
				// }
				if(func){
					func(json);
				}
			},

		},
		created() {
														this.get_user_session_declaration_personnel();
					this.get_list_user_declaration_personnel();
																					},
	}
</script>

<style>




</style>
